import Vue from 'vue'
import Router from 'vue-router'
import Vuex from 'vuex'
import Gists from '@/components/List/Gists'
import List from '@/components/List/List'
import Prism from 'prismjs'
import 'prismjs/themes/prism.css'

Vue.use(Prism)
Vue.use(Router);
Vue.use(Vuex);



export default new Router({
  // mode: 'history',
  routes: [
    { path: '/gists',
      name: 'Gists',
      component: Gists,
      children:[
        {
          path: 'gist',
          name: 'Gist',
          component: () => import('@/components/List/Gist')
        },
        {
          path:"addGist",
          name:"AddGist",
          component: () => import('@/components/List/AddGist')
        }
      ]
    },
    { path: '/list',
      name: 'List',
      component: List
    }
  ]
})



