<template>
  <div :class="{loading:true,hide:hideLoad}">
    <i class="el-icon-loading"></i>
    <span>页面加载中</span>
  </div>
</template>

<script>
  export default {
    name: "Load",
    props:["hideLoad"],
    data(){
      return {
      }
    }
  }
</script>

<style scoped>
  .loading {
    width:130px;
    margin: 0 auto;
  }

  .loading span {
    font-size: 16px;
    position: relative;
    top: -6px;
  }

  .loading i {
    font-size: 40px;
  }
</style>



